  margin: 0 10px;
}


.navbar {
    position: fixed;
    z-index: 100;
    margin: 0em;
    width: 100%;
    /*position: fixed;*/
    /*overflow: hidden;*/
    background-color: #333;
    /*position: fixed;  Set the navbar to fixed position */
    /*top: 0;  Position the navbar at the top of the page */
