}
*, *:before, *:after {
   box-sizing: inherit;
}

/* Links inside the navbar */
.navbar b {
    /*position: fixed;*/
/*    font-size: 15px;*/
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

/* Main content */
.main {
    /*margin-top: 30px;*/ /* Add a top margin to avoid content overlay */
}


.sizemenus {
 padding-left: 20px;
 padding-bottom: 20px;
}
.navbarwrap{
    padding-left: 2rem;
    padding-right: 2rem;
    /*padding-top: 50px !important;*/
  }
.sizeTextMenu{
    /*background:#EEF5F9 !important;*/
    font-size: 16px;
  }
.athiti {
    font-family: 'Athiti', sans-serif !important;
    font-weight: 500 !important;
    font-style: normal !important;
  }
.menus {
  height: 40px;
  width: 218px;
  margin-top: 10px;
}
.menubutton {
  background: none;
  color: white; 
  text-align:left;
  font-size: 14px;
  margin-left: -19px;
  width: 400px;
}

.listItem{
  color:#000 !important;
  text-align:left !important;
  padding-top: 13px !important;
  padding-bottom: 13px !important;
  width: 100% !important;
}


</style>



